$primary-color: #171618fa;
$secondary-color: #3f4446;
$tertiary-color: #e3eef0;
$fourth-color: #779c43;

form {
  display: flex;
  margin-right: 32px;
}

input {
  background-color: $tertiary-color;
  border-radius: 4px 0px 0px 4px;
  border-right: 0;
  color: #171618fa;
  font-size: 1.5em;
  height: 50px;
  text-align: center;
  width: 300px;
}

#search-btn {
  background-color: $secondary-color;
  border-left: 0;
  border-radius: 0px 4px 4px 0px;
  color: $tertiary-color;
  font-size: 1em;
  height: 50px;
  transition: .2s;
  width: 100px;
  &:hover {
    background-color: $primary-color;
    border: solid 2px $fourth-color;
    border-left: 0;
    color: $tertiary-color;
    cursor: pointer;
    font-size: 1.2em;
  }
}

@media only screen and (max-width: 850px) {
  form {
    margin: 0;
  }
}

@media only screen and (max-width: 500px) {
  input {
    width: 220px;
  }
}